<template>
  <div class="produk-view-show">
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
        <div class="container-fluid">
            <a class="navbar-brand" href="#" @click="$router.push({ path: '/' })">Kembali</a>
        </div>
    </nav>
    <div class="container mt-3">
      <div class="card" v-if="product">
        <div class="card-body">
          <div class="text-center mb-3">
            <img :src="$baseURL + 'storage/products/' + product.gambar" class="img-fluid" style="max-height: 300px">
          </div>
          <h5 class="card-title">{{ product.nama }}</h5>
          <p class="card-text text-start">{{ product.keterangan }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        product: null
      }
    },
    mounted(){
      this.axios.get(`product/${this.$route.params.id}`)
              .then((r) => {
                  if(r.data.status === 200){
                      this.product = r.data.payload
                  } else if (r.data.status === 401){
                      window.location.href = `${this.$baseURL}login`
                  }
              })
              .catch(() => {
                  this.$swal({
                      position: 'center',
                      icon: 'error',
                      title: 'Server Error',
                      text: 'Silakan hubungi Developer',
                      showConfirmButton: false,
                  })
              })
    }
  }
</script>